html {
  font-size: 16;
}

body {
  margin: 0;
  font-family: 'Nunito', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16;
}

/*Google Sans*/
@font-face {
  font-family: "Google Sans";
  src: local("Google Sans"), url('./fonts/GoogleSans-Bold.ttf') format("truetype");
  font-weight: 800;
}

